	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-04-27-people-for-the-ethical-treatment-of-yeast-pety/index.md absPath of file >>> MarkdownRemark","html":"<p>The next time you take a bite out of that yummy loaf of bread consider where it came from. Sure, cutting down all that nasty wheat is harmless enough. After all it's just a stupid plant. It doesn't have feelings. However, consider the condition of yeast.</p>\n<p>Here is how yeast is brought to market:</p>\n<p>How would you like to live in those conditions?</p>\n <img src=\"img_9274.jpg\" />\n<p>After that kind of treatment being introduced into a warm, nutrient rich environment like this:\n<a href=\"\"><img src=\"\" alt=\"Bread Dough\"></a>\nI am sure feels like heaven.</p>\n<p> []: <a href=\"http://www.flickr.com/photos/jasmic/302657002/\">http://www.flickr.com/photos/jasmic/302657002/</a></p>\n<p>but next comes this:\n<a href=\"\"><img src=\"\" alt=\"Bread Oven\"></a>\nat 450 degrees. I can hear the shriek of millions of individual yeasts shrieking out in terror like Joan of Arc as they face their final moments in a fiery furness.</p>\n<p> []: <a href=\"http://www.flickr.com/photos/bastian/2125007811/\">http://www.flickr.com/photos/bastian/2125007811/</a></p>\n<p>So next time you eat this:\n<a href=\"\"><img src=\"\" alt=\"Bread\"></a>\nJust think of the millions of dead bodies sacrificed so you can have light and fluffy bread.</p>\n<p> []: <a href=\"http://www.flickr.com/photos/hlk/389341090/\">http://www.flickr.com/photos/hlk/389341090/</a></p>\n<p>It's time to be PETY.</p>","frontmatter":{"title":"People for the Ethical Treatment of Yeast (PETY)","date":"April 27, 2008","image":{"childImageSharp":{"sizes":{"src":"/static/img_9274-c711ad88d84fb701550dcd6645a4eb8d-381d6.jpg"}}}}}},"pathContext":{"path":"/people-for-the-ethical-treatment-of-yeast-pety"}}

